import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { StoryModel } from '../models/StoryModel';

@Injectable({
    providedIn: 'root',
  })
  export class StoryService {

    private storiesUrl = 'https://hacker-news.firebaseio.com/v0/{0}.json?print=pretty';
  
    constructor(private http: HttpClient) { }

    private getBestStoryIds(): Observable<number[]>{
        return this.http.get<number[]>(this.storiesUrl.replace('{0}', 'beststories'));
    }

    getStories(): Observable<StoryModel[]> {
        var stories:StoryModel[] = [];

        this.getBestStoryIds().subscribe((ids)=>{
            for(var i = 0; i < ids.length; i++)
            {
                this.getStoryInfo(ids[i]).subscribe((story)=>{
                    stories.push(story);

                    if(stories.length > 1){
                        //re-sort array
                        stories.sort((a,b)=>{
                            if (a.id < b.id )
                                return -1;
                            if (a.id  > b.id )
                                return 1;
                            return 0;
                        });
                    }
                });
            }
        });
        
        return of(stories);
    }

    getStoryInfo(id:number): Observable<StoryModel>{
        return this.http.get<StoryModel>(this.storiesUrl.replace('{0}', 'item/'+id));
    }
  }