import { Component, Input, OnInit } from '@angular/core';
import { StoryModel } from '../models/StoryModel';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
    selector: 'news-sorter',
    templateUrl: './newsfilter.component.html',
    styleUrls:['./newsfilter.component.css']
})
export class NewsFilterComponent implements OnInit {
    sortVal: string = 'Default';
    @Input() public stories$:Observable<StoryModel[]>;
    public isLoading:boolean = true;

    constructor(){}
    ngOnInit(): void {
        this.stories$.subscribe(
            ()=>{},
            error=>{console.log(error);},
            ()=>{
                this.isLoading = false;
            }
        );
    }

    sortBy(action:string) {
        switch(action.toLowerCase()){
            case 'author':
                this.sortVal = 'Author';
                this.stories$ = this.stories$.pipe(map(items => items.sort((a,b)=>{
                    if (a.by < b.by)
                        return -1;
                    if (a.by > b.by)
                        return 1;
                    return 0;
                })));
            break;
            case 'score':
                this.sortVal = 'Score';
                this.stories$ = this.stories$.pipe(map(items => items.sort((a,b)=>{
                    if (a.score < b.score)
                        return -1;
                    if (a.score > b.score)
                        return 1;
                    return 0;
                })));
            break;
            default:
            this.sortVal = 'Default';
            this.stories$ = this.stories$.pipe(map(items => items.sort((a,b)=>{
                if (a.id < b.id)
                    return -1;
                if (a.id > b.id)
                    return 1;
                return 0;
            })));
            break;
        }
    }
}